import ReactDOM from 'react-dom';
import scrollToComponent from 'react-scroll-to-component-ssr';

import Layout from '../src/components/layout';
import Hero from '../src/components/hero';
import About from '../src/components/about';
import Products from '../src/components/products';
import Organic from '../src/components/organic';
import Certificate from '../src/components/certificate';
import LocationSection from '../src/components/location';
import Contact from '../src/components/contact';

import data from '../src/data/data';

class Index extends React.Component {

   constructor(props) {
      super(props);
      this.state = { modal: false }
   }

   handleScrollTo = section => {
      //const ref = ReactDOM.findDOMNode(this.refs[section]);

      // console.log('r: ', this[section].current);
      // scrollToComponent(this[section].current, {
      //    offset: 0,
      //    align: 'top',
      //    duration: 1000,
      //    ease: 'inOutSine'
      // });
   }

   handleModal = e => {
      const openModal = !this.state.modal;
      e.preventDefault();
      this.setState({ modal: openModal });
   }

   render() {

      const { menu, main: { home, about, products, organic, location } } = data;

      return (
         <Layout menu={menu} menuClick={this.handleScrollTo}>
            <Hero content={home} ref={el => this.inicio = el} />
            <About content={about} ref={el => this.nosotros = el} />
            <Products content={products} ref={el => this.productos = el} />
            <Organic content={organic} openModal={this.handleModal} />
            <LocationSection content={location} />
            <Contact ref={el => this.contacto = el} />
            <Certificate open={this.state.modal} closeModal={this.handleModal} />
         </Layout>
      )
   }
}

export default Index;



